import React, { Fragment, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import Pagination from "react-js-pagination";
import MetaData from "../layouts/MetaData";
import Slider from "rc-slider";
import "rc-slider/assets/index.css";
import Loader from "../layouts/Loader";
import { useDispatch, useSelector } from "react-redux";
import { getProducts } from "../../actions/productActions";
import Product from "./Product";
import { useAlert } from "react-alert";
const { createSliderWithTooltip } = Slider;
const Range = createSliderWithTooltip(Slider.Range);

const ProductMapping = ({ title }) => {
  const [currentPage, setCurrentPage] = useState(1);
  const [price, setPrice] = useState([1, 1000]);
  const [category, setCategory] = useState("");
  const [rating, setRating] = useState(0);
  const categories = [
    "Electronic",
    "Camera",
    "Laptop",
    "Accessories",
    "Headphone",
    "Food",
    "Book",
    "Clothes",
    "Shoes",
    "Beauty",
    "Health",
    "Sport",
    "Outdoor",
    "Home",
  ];

  const alert = useAlert();
  const dispatch = useDispatch();
  const { loading, products, error, productCount, resPerPage } = useSelector(
    (state) => state.products
  );

  const { keyword } = useParams();

  useEffect(() => {
    if (error) {
      return alert.error(error);
    }
    dispatch(getProducts(keyword, currentPage, price, category, rating));
  }, [dispatch, alert, error, keyword, currentPage, price, category, rating]);

  function setCurrentPageNo(pageNumber) {
    console.log(`active page is ${pageNumber}`);
    setCurrentPage(pageNumber);
  }

  return (
    <Fragment>
      {loading ? (
        <Loader />
      ) : (
        <Fragment>
          <MetaData title={"OwnitMart | Online shopping"} />
          <h2 id="products_heading">{title}</h2>
          <section id="products" className="container mt-2">
            <div className="row">
              {/* Filters Section - Only shown when there's a keyword */}
              {keyword && (
                <div className="col-md-3">
                  <div className="px-5">
                    <Range
                      marks={{ 1: `$1`, 1000: `$1000` }}
                      min={1}
                      max={1000}
                      defaultValue={[1, 1000]}
                      tipFormatter={(value) => `$${value}`}
                      tipProps={{
                        placement: "top",
                        visible: true,
                      }}
                      value={price}
                      onChange={(price) => setPrice(price)}
                    />
                    <hr className="my-5" />
                    <div className="mt-5">
                      <h4 className="mb-3">Categories</h4>
                      <ul className="pl-0">
                        {categories.map((category) => (
                          <li
                            style={{ cursor: "pointer", listStyle: "none" }}
                            key={category}
                            onClick={() => setCategory(category)}
                          >
                            {category}
                          </li>
                        ))}
                      </ul>
                    </div>
                    <hr className="my-3" />
                    <div className="mt-5">
                      <h4 className="mb-3">Ratings</h4>
                      <ul className="pl-0">
                        {[5, 4, 3, 2, 1].map((star) => (
                          <li
                            style={{ cursor: "pointer", listStyle: "none" }}
                            key={star}
                            onClick={() => setRating(star)}
                          >
                            <div className="rating-outer">
                              <div
                                className="rating-inner"
                                style={{ width: `${star * 20}%` }}
                              ></div>
                            </div>
                          </li>
                        ))}
                      </ul>
                    </div>
                  </div>
                </div>
              )}

              {/* Products Section */}
              <div className={keyword ? "col-md-9" : "col-12"}>
                <div className="row">
                  {products &&
                    products.map((product) => (
                      <Product
                        key={product._id}
                        product={product}
                        col={keyword ? 4 : 3}
                      />
                    ))}
                </div>
              </div>
            </div>
          </section>

          {/* Pagination Section */}
          {resPerPage <= productCount && (
            <div className="d-flex justify-content-center mt-5">
              <Pagination
                activePage={currentPage}
                itemsCountPerPage={resPerPage}
                totalItemsCount={productCount}
                onChange={setCurrentPageNo}
                nextPageText={"Next"}
                prevPageText={"Prev"}
                firstPageText={"First"}
                lastPageText={"Last"}
                itemClass="page-item"
                linkClass="page-link"
              />
            </div>
          )}
        </Fragment>
      )}
    </Fragment>
  );
};

export default ProductMapping;
